* {
    background-position: center top;
    /*margin: auto;*/
    font-family: system-ui,-apple-system,"Segoe UI",Roboto,"Helvetica Neue","Noto Sans","Liberation Sans",Arial,sans-serif,"Apple Color Emoji","Segoe UI Emoji","Segoe UI Symbol","Noto Color Emoji";
}


body {
    margin: 0;
    font-family: Arial, sans-serif;
}

.container {
    display: flex;
    max-width: 1920px;
    width: 95%;
}

.content {
    flex: 1;
    padding: 20px;
    width: 90%;
}

.card {
    border: 1px solid #ccc;
    border-radius: 5px;
    padding: 10px;
    margin-bottom: 20px;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
}

.card h3 {
    margin-top: 0;
}

.card p {
    margin-bottom: 0;
}

.gallery-container {
    position: relative;
    overflow: hidden;
    border-radius: 5px;
    margin-right: 10px;
}

.gallery-container img {
    display: none;
    width: 100%;
    height: auto;
}

.gallery-container img.active {
    display: block;
}

.gallery-button {
    position: absolute;
    bottom: 0%;
    transform: translateY(-50%);
    background: white;
    border: none;
    color: black;
    font-size: 24px;
    border-radius: 10px;
    cursor: pointer;
}

.gallery-button.prev {
    left: 10px;
}

.gallery-button.next {
    right: 10px;
}
.card-footer {
    display: flex;
    justify-content: space-between;
    align-items: flex-end;
}

.card-footer button{
    margin-left: 5px;
}

.favorite-button.favorite-active {
    background-color: red; /* Cambia el color de fondo a rojo cuando está marcado como favorito */
}

#loading-gif{
    /*display: none;*/
    position: absolute;
    z-index: 100;
    width: 100px;
    left: 55%;
    top: 55%;
}
