header,footer{
    width: 95%;
    background-color: #333;
    margin: 30px;
    padding: 30px;
    border-radius: 10px;
    /*color: #9e9e9e;*/
    color: #fff;
    text-align: center;
}
.header{
    background-color: #333;
    width: 95%;
    margin: 30px;
    height: 100px;
    border-radius: 10px;
    color: #9e9e9e;
    /*text-align: center;*/
}
.header .brand-logo{
    color: #fff;
    font-size: 24px;
    margin-left: 50px;
    margin-top: 10px;
    /*padding: 10px;*/
    text-align: center;
    text-transform: uppercase;
    font-weight: bold;
    letter-spacing: 2px;
    border: 0px;
    border-bottom: 1px solid #ccc;
}
.header .brand-logo:hover{
    cursor: pointer;
}
.hide-on-med-and-down{
    margin-top: 10px;
    margin-right: 50px !important;
    float: right;
}
.hide-on-med-and-down li
{
    border-radius: 5px;
    margin: 5px;
}
.hide-on-med-and-down li:hover{
    background-color: #555;
}
.icon-container {
    position: relative;
    display: inline-block;
}

.icon-container .material-icons {
    font-size: 24px;
    vertical-align: middle;
}

.icon-container .favorite-counter {
    position: absolute;
    top: -8px;
    right: -8px;
    background-color: red;
    color: white;
    border-radius: 50%;
    padding: 4px;
    font-size: 14px;
    min-width: 16px;
    text-align: center;
}



footer label{
    font-size: 16px;
    color: white;
    margin-top: 10px;
}